nav {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar_container {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.navbar_buttons {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.navbar_button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
  margin: 0.5rem;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
.navbar_button:hover {
  background-color: #f2f2f2;
}

.navbar_search-box {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.5rem;
  transition: all 0.2s ease-in-out;
}
.navbar_search-box:hover {
  border: 1px solid #f2f2f2;
  outline: none;
}
.navbar_search-box .input {
  border-bottom: 1px solid #000000;
  background: transparent;
}
.navbar_search-box .icon {
  display: inline;
}
.navbar_search-box input {
  border: 0;
  background: transparent;
}
.navbar_search-box input:focus-visible {
  outline: none;
}

.navbar-current::after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background-color: #f2f2f2;
  transition: all 0.2s ease-in-out;
}

.navbar-link-hover:hover::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background-color: #f2f2f2;
  transition: all 0.2s ease-in-out;
}

.custom-input {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.custom-input div {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding: 0.5rem 0.5rem 0.5rem 0.5rem;
  align-content: bottom;
}
.custom-input label {
  font-weight: bolder;
  font-size: 1.2rem;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.custom-input input {
  border: none;
  border-bottom: 1px solid #787878;
  border-radius: 0;
  box-shadow: none;
  font-size: 1rem;
  padding: 0.5rem 0.5rem;
  width: 80%;
  transition: border-bottom 0.3s ease-in-out;
  margin: 0.5rem 0.5rem 0.5rem 0.8rem;
}
.custom-input input:focus {
  outline: none;
  border-bottom: 2px solid #000;
}
.custom-input input::placeholder {
  color: #3b3b3b;
  padding: 0.5rem 0;
}
.custom-input .error {
  border-bottom: 2px solid #f00;
  background: #fdd;
}
.custom-input .error-message {
